import * as $ from "jquery";
import TomSelect from "tom-select";
import swal from "sweetalert";

// document.cookie = "AC-C=ac-c;expires=Fri, " + Date() + ";path=/;SameSite=Lax";
// $(document).ready(function () {
// reset();
// });
let obj = {
  "Programmation Web": 2,
  "Programmation mobile": 3,
  "Services Web": 2,
  "Interface Homme Machine": 2,
  "Méthodes Agiles": 3,
};
const cne_regex = new RegExp("^\\w\\d{9}$");
const nom_regex = new RegExp("^(\\w+\\s\\w+)(\\s\\w+){0,2}\\w$");
let current_index = NaN;
const eventHandler = function () {
  return function () {
    $("#Coefficient").val(obj[this.getValue()]);
  };
};
let tom_select = new TomSelect("#Matiere", {
  create: false,
  sortField: { field: "text" },
  onChange: eventHandler(),
});
$(".input-form").each(function (index) {
  index = ++index;
  $(this).on("blur keyup", function () {
    if ($(this).val().length === 0) {
      $(this).removeClass("border-gray-300");
      $("#warn-req-" + index).removeClass("hidden");
      $("#warn-req-" + index).addClass("block");
      $(this).addClass("border-red-600");
    } else {
      $(this).addClass("border-gray-300");
      $("#warn-req-" + index).addClass("hidden");
      $("#warn-req-" + index).removeClass("block");
      $(this).removeClass("border-red-600");
    }
  });
});
const reset = () => {
  $("#CNE").val("");
  $("#Nom_prenom").val("");
  tom_select.clear(true);
  $("#Coefficient").val("");
  $("#Note").val("");
};
const calculer_moyenne = () => {
  if ($(`.notes`)) {
    $("#moyenne").html(
      Math.round(
        ($(".notes")
          .toArray()
          .reduce((acc, element) => (acc += parseInt(element.innerHTML)), 0) /
          $(".notes").length) *
          100
      ) / 100
    );
  }
};
$("#Ajouter").on("click", () => {
  let object = [];
  let tests = {
    CNE: cne_regex.test($("#CNE").val().trim()),
    "Nom & prenom": nom_regex.test($("#Nom_prenom").val().trim()),
    Coefficient: obj[$(".item").text().trim()] == $("#Coefficient").val(),
    Note: parseInt($("#Note").val()) <= 20 && parseInt($("#Note").val()) >= 0,
  };

  if (Object.values(tests).reduce((a, b) => a + b) == 4) {
    let tr =
      $(`<tr class="cursor-pointer hover:bg-slate-300 border-b-2 py-4 px-6 border-b-gray-300">
    <td class="py-4 px-6 CNES">${$("#CNE").val().trim()}</td>
    <td class="py-4 px-6 Noms">${$("#Nom_prenom").val().trim()}</td>
    <td class="py-4 px-6 Matieres">${$(".item").text().trim()}</td>
    <td class="py-4 px-6 text-center">${$("#Coefficient").val()}</td>
    <td class="py-4 px-6 text-center">${$("#Note").val().trim()}</td>
    <td class="py-4 px-6 notes text-center">${$("#Note").val().trim()}</td>
  </tr>`).on("click", function () {
        const temp = $(this).text().trim().split("\n");
        current_index = $(this).index();
        $("#CNE").val(temp[0]);
        $("#Nom_prenom").val(temp[1].trim());
        tom_select.setValue(temp[2].trim(), true);
        $("#Coefficient").val(temp[3].trim());
        $("#Note").val(temp[4].trim());
      });
    const MATIERES = $(".Matieres").toArray();
    const CNES = $(".CNES").toArray();
    const NOMS = $(".Noms").toArray();
    // const CNES_MATIERES =;
    const matieres_cne = (cne) => {
      return CNES.map((e, index) => {
        if (e.innerHTML == cne) return MATIERES[index].innerText;
      }).filter((e) => e != undefined);
    };
    object = CNES.reduce((accumulator, element, index) => {
      if (!object.includes(element.innerHTML))
        return {
          ...accumulator,
          [element.innerHTML]: [
            ...matieres_cne(element.innerHTML),
            NOMS[index].innerHTML,
          ],
        };
    }, {});
    if (
      object[$("#CNE").val().trim()] == undefined ||
      (!object[$("#CNE").val().trim()].includes($(".item").text().trim()) &&
        object[$("#CNE").val().trim()].includes(
          $("#Nom_prenom").val().trim()
        )) ||
      (object[$("#CNE").val().trim()].includes($(".item").text().trim()) &&
        !object[$("#CNE").val().trim()].includes($("#Nom_prenom").val().trim()))
    ) {
      $("#tbody").append($(tr));
      reset();
      calculer_moyenne();
    } else if (object[$("#CNE").val().trim()][0] == $(".item").text().trim()) {
      swal({
        title: "Attention!",
        text: "vous ne pouvez pas affecter la même matière a ce étudiant",
        icon: "error",
        dangerMode: true,
      });
    } else if (
      object[$("#CNE").val().trim()][1] != $("#Nom_prenom").val().trim()
    ) {
      swal({
        title: "Attention!",
        text: "vous ne pouvez pas affecter la different nom a cette CNE",
        icon: "error",
        dangerMode: true,
      });
    }
  } else {
    let problems = Object.keys(tests).filter((e) => {
      if (tests[e] == false) return e;
    });
    swal({
      title: "Attention!",
      text:
        "les Champs suivant ne répond pas aux exigences: \n le champ: " +
        problems.join(" \nle champ: "),
      icon: "error",
      dangerMode: true,
    });
  }
});
$("#Modifier").on("click", () => {
  if (isNaN(current_index)) {
    swal({
      title: "Attention!",
      text: "Cliquer sur une tuple",
      icon: "info",
    });
  } else {
    let tests = {
      CNE: cne_regex.test($("#CNE").val().trim()),
      "Nom & prenom": nom_regex.test($("#Nom_prenom").val().trim()),
      Coefficient: obj[$(".item").text().trim()] == $("#Coefficient").val(),
      Note: parseInt($("#Note").val()) <= 20 && parseInt($("#Note").val()) >= 0,
    };
    if (Object.values(tests).reduce((a, b) => a + b) == 4) {
      let temp_tr =
        $(`<tr class="cursor-pointer hover:bg-slate-300 border-b-2 py-4 px-6 border-b-gray-300">
    <td class="py-4 px-6 CNES">${$("#CNE").val().trim()}</td>
    <td class="py-4 px-6 Noms">${$("#Nom_prenom").val().trim()}</td>
    <td class="py-4 px-6 Matieres">${$(".item").text().trim()}</td>
    <td class="py-4 px-6 text-center">${$("#Coefficient").val()}</td>
    <td class="py-4 px-6 text-center">${$("#Note").val().trim()}</td>
    <td class="py-4 px-6 notes text-center">${$("#Note").val().trim()}</td>
  </tr>`).on("click", function () {
          const temp = $(this).text().trim().split("\n");
          current_index = $(this).index();
          $("#CNE").val(temp[0]);
          $("#Nom_prenom").val(temp[1].trim());
          tom_select.setValue(temp[2].trim(), true);
          $("#Coefficient").val(temp[3].trim());
          $("#Note").val(temp[4].trim());
        });
      if (current_index == 0) {
        $("#tbody > tr").eq(current_index).remove();
        $("#tbody").prepend(temp_tr).index = 0;
        reset();
      } else {
        $("#tbody > tr").eq(current_index).remove();
        $("#tbody > tr")
          .eq(current_index - 1)
          .after(temp_tr);
        reset();
      }

      current_index = NaN;
      calculer_moyenne();
    }
  }
});

$("#Retirer").on("click", () => {
  if (isNaN(current_index)) {
    swal({
      title: "Attention!",
      text: "Cliquer sur une tuple",
      icon: "info",
    });
  } else {
    $("#tbody > tr").eq(current_index).remove();
  }
});
